/* Hides swiper lazy loading */
.swiper-lazy-preloader {
    animation: none;
    transform: none;
    display: none;
    visibility: hidden;
}
.swiper-button-disabled {
    display: none;
}

/* GetWid Slick Carousel arrows */
.slick-next::before,
.slick-prev::before {
    font-family: 'dashicons';
    font-size: 30px;
    color: var(--tainacan-block-primary, #298596);
}
.slick-next::before {
    content: '\f344';
}
.slick-prev::before {
    content: '\f340'
}
.slick-arrow {
    width: 42px;
    height: 42px;
}
.slick-next.slick-disabled,
.slick-prev.slick-disabled {
    display: none;
    visibility: hidden;
}

/* Makes backround image on header multiply */
.ct-header [data-row*="middle"] {
    background-blend-mode: multiply;
    --menu-indicator-height: 4px;
}

/* Puts the breadcrumb on top of the page */
.hero-section {
    position: relative;
    padding-right: 64px;
}
@media screen and (max-width: 1000px) {
    .hero-section {
        padding: 12px;
    }
}
.hero-section .ct-breadcrumbs {
    position: absolute;
    top: 6px;
    z-index: 2;
}
[data-vertical-spacing*="top"] .hero-section .ct-breadcrumbs {
    top: calc(-1 * var(--content-vertical-spacing) - 1rem);
}
[data-vertical-spacing*="top"] .hero-section .ct-breadcrumbs:only-child {
    top: calc(-1 * var(--content-vertical-spacing));
}

/* Custom Header elements */
.brasiliana-header-elements {
    display: flex;
    flex-direction: column;
    z-index: 2;
    max-width: var(--block-max-width, var(--container-max-width));
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    align-items: flex-end;
    position: relative;
    z-index: 2;
}

.brasiliana-header-elements .brasiliana-header-button {
    border: 1px solid var(--palette-color-11, #FFEBBD);
    background-color: transparent;
    border-radius: 100%;
    padding: 0.5rem 1.2rem 0.5rem 0.8rem;
    height: 56px;
    width: 56px;
    margin-bottom: 8px;
    margin-left: 8px;
    position: absolute;
    cursor: pointer;
}
.brasiliana-header-elements .brasiliana-header-button:first-child {
    top: 6px;
}
.brasiliana-header-elements .brasiliana-header-button:last-child {
    top: calc( var(--content-vertical-spacing) + 8px);
}
[data-vertical-spacing*="top"] .brasiliana-header-elements .brasiliana-header-button:first-child {
    top: calc((-1 * var(--content-vertical-spacing)) + 6px);
}
[data-vertical-spacing*="top"] .brasiliana-header-elements .brasiliana-header-button:last-child {
    top: 8px;
}
.brasiliana-header-elements .brasiliana-header-button::hover,
.brasiliana-header-elements .brasiliana-header-button::focus {
    border: 1px solid var(--palette-color-11, #FFEBBD);
}
.brasiliana-header-elements .brasiliana-header-button .icon {
    position: relative;
}
.brasiliana-header-elements .brasiliana-header-button .icon i::before {
    font-size: 1.5rem;
    line-height: 1rem;
    color: var(--paletteColor1, #642D39);
}
.brasiliana-header-elements .brasiliana-header-button .icon i::after {
    background-color: var(--paletteColor1, #642D39);
}
.brasiliana-header-elements .brasiliana-header-button:hover .icon i::before {
    color: var(--paletteColor2, #864453);
}
.brasiliana-header-elements .brasiliana-header-button:hover .icon i::after {
    background-color: var(--paletteColor2, #864453);
}
.brasiliana-header-elements .brasiliana-header-button:focus .icon i::before {
    color: var(--paletteColor2, #864453);
}
.brasiliana-header-elements .brasiliana-header-button:focus .icon i::after {
    background-color: var(--paletteColor2, #864453);
}
.brasiliana-header-elements .brasiliana-header-button .icon i.dashicons {
    line-height: 1.875rem;
    width: 1.75rem;
}

/* Header buttons inside dark backrounds */
body.single-curadorias .brasiliana-header-elements .brasiliana-header-button .icon i::before,
header.tainacan-collection-header:not(.tainacan-collection-header--term-page) .brasiliana-header-elements .brasiliana-header-button .icon i::before {
