body.single-curadorias .brasiliana-header-elements .brasiliana-header-button:hover .icon i::before,
header.tainacan-collection-header:not(.tainacan-collection-header--term-page) .brasiliana-header-elements .brasiliana-header-button:hover .icon i::before {
    color: var(--palette-color-11, #FFEBBD);
}
body.single-curadorias .brasiliana-header-elements .brasiliana-header-button:hover .icon i::after,
header.tainacan-collection-header:not(.tainacan-collection-header--term-page)  .brasiliana-header-elements .brasiliana-header-button:hover .icon i::after {
    background-color: var(--palette-color-11, #FFEBBD);
}
body.single-curadorias .brasiliana-header-elements .brasiliana-header-button:focus .icon i::before,
header.tainacan-collection-header:not(.tainacan-collection-header--term-page) .brasiliana-header-elements .brasiliana-header-button:focus .icon i::before {
