/* Brasiliana Social Icons share list */
.brasiliana-social-icons {
    position: absolute;
    display: none;
    visibility: hidden;
    width: 56px;
    left: 0;
    display: flex;
    justify-content: center;
    opacity: 0.0;
    top: 90%;
    transition: top 0.3s ease, opacity 0.3s ease;
}
.brasiliana-social-icons.is-list-open {
    display: flex;
    visibility: visible;
    opacity: 1.0;
    top: calc(100% + 8px);
}
.brasiliana-social-icons [data-icons-type] > * {
    padding: 12px;
    border: 1px solid var(--palette-color-11, #FFEBBD);
    background-color: white;
    display: block;
    border-radius: 100%;
    margin-bottom: 0px;
    --icon-color: var(--paletteColor1, #642D39);
    transition: margin 0.3s ease;
}
.brasiliana-social-icons.is-list-open [data-icons-type] > * {
    margin-bottom: 8px;
}

/* Single post header for curadorias */
body.single-curadorias .hero-section .page-description {
    max-width: 624px;
}

